<template>
  <Lay :route="route" :arr="arr" :url="url" :method="method"/>
</template>

<script>
import Lay from '@/components/common/template';
export default {
  props: ["route"],
  data () {
    return {
      method: 'get',
      url: '/singlewell/screw',    
      arr: [
        {
          txt: '序号',
          prop: 'no'
        },
        {
          txt: '螺杆型号',
          prop: 'screw_model'
        },
        {
          txt: '日期',
          child: [
            {
              txt: '入井时间',
              prop: 'start_at'
            },
            {
              txt: '出井日期',
              prop: 'end_at'
            }
          ]
        },
        {
          txt: '第几次下井',
          prop: 'down_times'
        },
        {
          txt: '井段',
          child: [
            {
              txt: '入井井深（m）',
              prop: 'start_depth'
            },
            {
              txt: '出井井深（m）',
              prop: 'end_depth'
            },
            {
              txt: '进尺（m）',
              prop: 'footage'
            }
          ]
        },
        {
          txt: '时间',
          child: [
            {
              txt: '纯钻时间（h）',
              prop: 'drilling_time'
            },
            {
              txt: '循环/扩划眼时间（h）',
              prop: 'cycle_ream_time'
            },
            {
              txt: '工作时间（h）',
              prop: 'work_time'
            },
            {
              txt: '合计时间（h）',
              prop: 'total_time'
            }
          ]
        },
        {
          txt: '钻井方式',
          prop: 'drill_type'
        },
        {
          txt: '钻井参数',
          child: [
            {
              txt: '钻压（kN）',
              prop: 'wob'
            },
            {
              txt: '转速（rpm）',
              prop: 'rpm'
            },
            {
              txt: '排量（L/s）',
              prop: 'flow'
            },
            {
              txt: '泵压（MPa）',
              prop: 'pump_pressure'
            }
          ]
        },
        {
          txt: '钻井液',
          child: [
            {
              txt: '体系（油基/水基）',
              prop: 'fluid_system'
            },
            {
              txt: '密度（g/cm³）',
              prop: 'density'
            },
            {
              txt: '粘度（mPa.s）',
              prop: 'viscosity'
            },        
          ]
        },
        {
          txt: '起出原因',
          prop: 'pullout_reason'
        }
      ]
    }
  },

  components: {
    Lay
  },

  mounted: function() {
  
  },

  methods: {
  }
}
</script>



